.loja-page {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.loja-header {
  background: linear-gradient(rgba(18, 18, 18, 0.85), rgba(31, 41, 55, 0.85)), url('../../public/images/MilSim_4.jpeg') center/cover no-repeat;
  background-attachment: fixed;
  color: white;
  padding: 3rem 2rem;
  text-align: center;
  border-bottom: 4px solid #D4AF37;
}

.loja-header h1 {
  font-size: 2.5rem;
  margin: 0 0 0.5rem 0;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: bold;
}

.loja-header p {
  font-size: 1.1rem;
  margin: 0;
  color: #D4AF37;
}

.loja-content {
  flex: 1;
  background: linear-gradient(rgba(245, 245, 245, 0.92), rgba(245, 245, 245, 0.92)), url('../../public/images/MilSim_4.jpeg') center/cover no-repeat;
  background-attachment: fixed;
  padding: 2.5rem 1.5rem;
}

.loja-title {
  display: none;
}

.loja-filters {
  max-width: 900px;
  margin: 0 auto 2rem auto;
  display: flex;
  gap: 1rem;
  justify-content: center;
  flex-wrap: wrap;
}

.loja-filters input,
.loja-filters select {
  padding: 0.8rem 1rem;
  border: 1px solid #ddd;
  border-radius: 6px;
  font-size: 0.95rem;
}

.loja-grid {
  max-width: 1100px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 1.5rem;
}

.loja-error {
  background: #ffe4e6;
  color: #9f1239;
  border: 1px solid #fda4af;
  padding: 0.6rem 0.8rem;
  border-radius: 6px;
  margin: 0 auto 1rem auto;
  max-width: 900px;
}

.loja-loading {
  background: #fff3cd;
  color: #664d03;
  border: 1px solid #ffecb5;
  padding: 0.6rem 0.8rem;
  border-radius: 6px;
  margin: 0 auto 1rem auto;
  max-width: 900px;
}

.product-card {
  background: #fff;
  border-radius: 8px;
  padding: 1.2rem;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  display: flex;
  flex-direction: column;
  gap: 0.7rem;
  transition: all 0.3s;
}

.product-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.15);
}

.product-thumb {
  width: 100%;
  height: 140px;
  background: #eee;
  border-radius: 8px;
}

.product-category {
  font-size: 0.85rem;
  color: #666;
}

.product-price {
  font-weight: bold;
  color: #121212;
}

@media (max-width: 768px) {
  .loja-header h1 {
    font-size: 1.8rem;
  }

  .loja-content {
    padding: 1.5rem 1rem;
  }

  .loja-grid {
    grid-template-columns: 1fr 1fr;
  }
}
